.nav-container{
    display: flex;
    height: 72px;
    align-items: center;
    background: #1d2020;
    margin: 0;
    justify-content: space-between;
    z-index: 99;
    box-shadow: 0 1px 3px rgba(0,0,0,12), 0 1px 2px rgb(0, 0, 0.26);
}

.nav-left{
    display: flex;
    align-items: center;
}
.nav-right{
    margin-right: 40px;
    

}

.flash-logo{
    margin-left:20px;
    height: 40px;

}
.flash-logo-text{
    font-family: "Bangers",cursive;
    font-size: 60px;
    letter-spacing: 1px;
    margin: 0 10px;
    color: #e7e6e7;
}
.nav-right-par{
    font-family: "Bangers",cursive;
    font-size: 35px;
    letter-spacing: 0.5px;
    color: #e7e6e7;
}